<template>
	<div class="upPhone">
		<!--更换手机号码返回-->
		<div class="upPhone-back">
			<div class="left" @click="backGo"><img src="/img/wode/shezhi/shezhi-back.png" alt="" /></div>
			<div class="right">更换手机号码</div>
		</div>
		<!--更换手机号码-->
		<div class="upPhone-items">
			<div class="item">
				<div class="item-left">当前手机号</div>
				<div class="item-right" v-text="this.$store.state.user.username"></div>
			</div>
			<div class="item item2">
				<input type="text" placeholder="请输入您的手机号" v-model="username" />
				<div class="getYzm">获取验证码</div>
			</div>
			<div class="item2 item3">
				<input type="text" placeholder="请输入验证码" />
			</div>
		</div>
		<!--提交-->
		<div class="upPhone-bottom">
			<div class="tj" @click="tj">提交</div>
		</div>
		<!-- 弹出层-->
		<div class="tanchuceng" v-show="show"></div>
		<div class="tanchu-logo" v-show="show"><img src="/img/shezhi-logo2.png" alt="" /></div>
		<div class="tanchu" v-show="show">
			<div class="tanchu-top">
				<div class="tanchu-title">温馨提示</div>
				<div class="tanchu-content">您确定要注销账户吗？</div>
			</div>
			<div class="tanchu-bottom">
				<div @click="tanChuFalse" class="tanchu-left">我再想想</div>
				<div @click="tanChuEnd" class="tanchu-right">狠心离开</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: "upPhone",
		data: function(){
			return {
				show:false,
				username: ''
			}
		},
		methods:{
			backGo(){
				this.$router.go(-1)
			},
			// 提交事件
			tj(){
				console.log('提交事件')
				this.show = true
				var mo = function(e) {
					e.preventDefault()
				}
				document.body.style.overflow = 'hidden'
				document.addEventListener('touchmove', mo, false)
			},
			// 弹出层 我再想想
			tanChuFalse(){
				this.show = false
				var mo = function(e) {
					e.preventDefault()
				}
				document.body.style.overflow = ''
				document.addEventListener('touchmove', mo, false)
			},
			// 弹出层 狠心离开
			tanChuEnd(){
				this.show = false
				var mo = function(e) {
					e.preventDefault()
				}
				document.body.style.overflow = ''
				document.addEventListener('touchmove', mo, false)
				this.$store.commit('clearUser')
				this.$router.push('/')
			}
		}
	}
</script>

<style scoped>
